export default (value) => {
  const date = new Date(value)

  const year = date.getFullYear()
  const dayWeek = date.getDay()
  const month = date.getMonth()
  const day = date.getDate()

  const months = [
    'Stycze',
    'Luty',
    'Marzec',
    'Kwiecie',
    'Maj',
    'Czerwiec',
    'Lipiec',
    'Sierpie',
    'Wrzesie',
    'Padziernk',
    'Listopad',
    'Grudzie'
  ]

  const days = [
    'Pon',
    'Wt',
    'r',
    'Czw',
    'Pt',
    'Sob',
    'Niedz'
  ]

  return `${days[dayWeek]}, ${day} ${months[month]} ${year}`
}